'use strict';

//require('./jacube.scss');
//require('./index.html');

import { Elm } from "./Need2Learn.elm";

import questions from './questions.json';

const flags = { questions : questions, data : JSON.parse(localStorage.getItem('need2LearnData')) || null };
console.log(flags);
const app = Elm.Need2Learn.init({
    flags: flags
});

app.ports.toLocalStorage.subscribe((data) => {
    localStorage.setItem('need2LearnData', JSON.stringify(data));
});



